import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { ApprovalService } from '../../angular-services/approval.service';

@Component({
  selector: 'app-new-approval-form-admin',
  templateUrl: './new-approval-form.component.html',
  styleUrls: ['./new-approval-form.component.css']
})
export class NewApprovalFormComponent implements OnInit {

  formNewApproval: FormGroup;
  mediaType = 'approvals';
  alert: any = { type: '', message: '', show: false };

  constructor(private approvalService: ApprovalService) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.formNewApproval = new FormGroup({
      name: new FormControl('', Validators.required),
      amount: new FormControl(0, Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  save() {
    if (this.formNewApproval.valid) {
      let data = this.formNewApproval.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.approvalService.addApproval(data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }
}
