import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewApprovalPageComponent } from './new-approval-page.component';

const routes: Routes = [
  { path: '', component: NewApprovalPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewApprovalPageRoutingModule { }
